<template>
  <div class="root">
    <div ref="infoBox1" class="info-box1" @click="infoBox1()"></div>
    <div ref="infoBox2" class="info-box2" @click="infoBox2()"></div>
    <div class="form-box">
      <el-form :model="loginForm" :rules="loginRules" ref="loginRef">
        <div class="login-title"><h1>密码登录</h1></div>
        <el-form-item prop="username">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-user-solid"></i>
              </i>
            </span>
            <el-input 
              v-model="loginForm.username"
              placeholder="Username"
              name="username"
              type="text"
              tabindex="1"
              auto-complete="on"
            ></el-input>
          </div>
        </el-form-item>
        <el-form-item prop="password">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-lock"></i>
              </i>
            </span>
            <el-input 
              key="password"
              ref="password"
              v-model="loginForm.password"
              placeholder="Password"
              name="password"
              type="password"
              tabindex="2"
              auto-complete="on"
              show-password
            ></el-input>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="LoginSubmit">登录</el-button>
      </div>
      <el-link type="info" @click="$router.push('/register')" style="margin-top:10px; color: #fff; margin-top: 25px;font-size: 10px;">还没有账号? 前往注册></el-link>
    </div>
  </div>

</template>

<script>
import { loginAPI } from '@/api'
import { mapMutations } from 'vuex'
export default {
  name: 'login',
  data(){
    return{
      dialogFormVisible: false,
      loginForm:{
        username: '',
        password: '',
      },
      loginRules:{
        username:[
          {required:true, message:'账号不能为空', trigger:'blur'},
          {
            pattern: /^(?!(\d+)$)[a-zA-Z\d_]{4,20}$/,
            message: "账号长度4-20，必须包括数字、字母，可包括下划线",
            trigger: "blur"
          },
        ],
        password:[
          {required:true, message:'密码不能为空', trigger:'blur'},
          {
            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
            message: "密码长度为6-20位，必须且只能包括数字、字母",
            trigger: "blur"
          }
        ]
      }
    }
  }, 
  mounted(){

  },
  methods: {
    ...mapMutations(['updateToken']),
    //登录事件
    LoginSubmit(){
      this.$refs.loginRef.validate(async valid => {
        if(valid){
          const { data: res } = await loginAPI(this.loginForm)
          // console.log(res);
          //登陆失败
          if(res.status !== 0) return this.$message.error(res.message)
          //登陆成功
          this.$message.success(res.message)
          this.updateToken(res.token)
          localStorage.setItem('token',res.token)
          const token = localStorage.getItem('token')
          this.$store.commit('updateToken',token)
          this.$store.dispatch('getUserInfoActions')
          }else{
          return false;
        }
      })
    },
    infoBox1(){
      this.$refs.infoBox1.style.display = 'none'
      this.$refs.infoBox2.style.display = 'block'
    },
    infoBox2(){
      this.$refs.infoBox1.style.display = 'block'
      this.$refs.infoBox2.style.display = 'none'
    }
  }
}
</script>

<style scoped>
.root{
  display: flex;
  justify-content: space-around;
}

.info-box1{
  width: 40%;
  /* height: 100px; */
  background: url(./image/loginPic.png);
  background-size: 100% 100%;
  /* background: #fff; */
}
.info-box2{
  background: url(./image/二维码.jpg);
  background-size: 100% 100%;
  width: 40%;
  /* height: 100px; */
  display: none;
  /* opacity: 0; */
}
.form-box{
  width: 40%;
}
.login-title{
  text-align: center;
}
.login-title h1{
  color: #fff;
  font-family: "XingKai";
  font-size: 28px;
}
.el-form-item {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
  margin: 26px 0;
}
.el-container{
    display: flex;
    border-radius: 5px;
}
.icon-container{
    padding: 0 10px;
    color: #fff;
    font-size: 20px;
}
.el-input /deep/ .el-input__inner{
    border: none;
    background: #0000;
    color: #fff;
}
.el-button{
  width: 100%;
}
.el-button--primary{
  background-color: #7a6041;
  border: #7a6041;
}
.el-button--primary:hover{
  background-color: #563b1d;
  border: #563b1d;
}
</style>