<template>
  <div>
    <nav ref="nav">
        <div class="menu-icon"><i ref="menuBtn" class="el-icon-menu" @click="menuBtnClick()"></i></div>
        <!-- <div class="logo" ref="logo"><span ref="logoSpan">GORGEOUS </span>NBA</div> -->
        <div class="logoPic" ref="logoPic">
          <img ref="logoPicB" src="@/assets/logo大.png" alt="">
          <img ref="logoPicS" style="display: none; top: 5px;" src="@/assets/logo小.png" alt="">
        </div>
        <div class="nav-items" ref="items">
            <li><router-link to="/">首页</router-link></li>
            <li><router-link to="/test">中药方剂</router-link></li>
            <li><router-link to="/aboutus">增加方剂</router-link></li>
            <li><router-link to="/waterfall">发现</router-link></li>
            <li><router-link to="/creator">创作者服务</router-link></li>
        </div>
        <div class="search-icon" ref="searchBtn" @click="searchBtnClick()"><i class="el-icon-search"></i></div>
        <div class="cha-icon" ref="chaBtn" @click="chaBtnClick()"><i class="el-icon-close"></i></div>
        <form action="#" ref="form">
          <input v-model="input" type="search" class="search-data" placeholder="搜索文章内容...">
          <button type="submit"  @click.prevent="SearchArticles()"><i class="el-icon-search"></i></button>
        </form>
        <div class="nav-login">
          <!-- Form -->
          <el-button style="  font-size: 30px; color: #fff;" type="text" @click="dialogFormVisible = true"><i class="el-icon-s-custom" aria-hidden="true"></i></el-button>

          <el-dialog :visible.sync="dialogFormVisible" :append-to-body="true">
            <Login></Login>
          </el-dialog>
        </div>
        <div class="nav-name">
          <img :src="user_pic" v-if="user_pic" alt="" class="avatar_pic">
          <el-dropdown>
            <span class="el-dropdown-link">
              {{nickname}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="toPersonal()">个人中心</el-dropdown-item>
              <el-dropdown-item @click.native="quitLogin()">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
    </nav>
  </div>
</template>

<script>
import Login from '@/views/Login/index.vue'
import { mapGetters } from 'vuex'
export default {
  components:{Login},
  name:'Header',
  data() {
    return{
      input:'',
      list:[],
      dialogFormVisible: false,
      formLabelWidth: '100px'
    }
  },
  computed:{
    ...mapGetters(['username','nickname','user_pic','desc'])
  },
  created(){
    window.addEventListener('scroll',this.handleScroll);
  },
  mounted(){
  },
  methods:{
    handleScroll(){
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // console.log(scrollTop,'滚动距离');
      if(scrollTop > 0){
        this.$refs.nav.classList.add("navActive")
        this.$refs.logoPic.classList.add("logoActive")
        this.$refs.logoPicB.style.display = 'none'
        this.$refs.logoPicS.style.display = 'block'
        // this.$refs.logo.classList.add("navLogoActive")
        // this.$refs.logoSpan.classList.add("navLogoActive")

      }else if(scrollTop == 0){
        this.$refs.nav.classList.remove("navActive")
        this.$refs.logoPic.classList.remove("logoActive")
        this.$refs.logoPicB.style.display = 'block'
        this.$refs.logoPicS.style.display = 'none'
        // this.$refs.logo.classList.remove("navLogoActive")
        // this.$refs.logoSpan.classList.remove("navLogoActive")
      }
    },
    beforeDestroy () {
    	window.removeEventListener('scroll', this.handleScroll)
    },
    menuBtnClick(){
      this.$refs.items.classList.add("active")
      this.$refs.menuBtn.classList.add("hide")
      this.$refs.searchBtn.classList.add("hide")
      this.$refs.chaBtn.classList.add("show")
    },
    chaBtnClick(){
      this.$refs.items.classList.remove("active")
      this.$refs.menuBtn.classList.remove("hide")
      this.$refs.searchBtn.classList.remove("hide")
      this.$refs.chaBtn.classList.remove("show")
      this.$refs.form.classList.remove("active")
      this.$refs.chaBtn.style.color = "#ff3d00"
    },
    searchBtnClick(){
      this.$refs.form.classList.add("active")
      this.$refs.searchBtn.classList.add("hide")
      this.$refs.chaBtn.classList.add("show")
    },
    toPersonal(){
      this.$router.push('/personal')
    },
    quitLogin(){
      this.$confirm('确定退出吗？真的要走吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          //清除vuex
          this.$store.commit('updateToken','')
          this.$store.commit('updateUserInfo',{})
          window.localStorage.clear()
          this.$message({
            type: 'success',
            message: '已退出登录!'
          });
          this.dialogFormVisible = true;
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消退出'
          });          
        });
    },
    SearchArticles(){
      this.$axios.post('/api/SearchArticlesInfo',{input:this.input}).then(res=>{
        console.log(res);
        if(res.data.status == 200){
          this.$store.state.list = res.data.data
          if(this.$route.path !== '/waterfall'){
            this.$router.push({
              path:'/waterfall',
              query:{
                input: this.input
              }
            })
          }
        }else{
          this.$message({
            type: 'error',
            message: '未搜索到相关文章，试试搜点别的？'
          })
        }
      })
    }

    // //停止页面滚动
    // stopMove(){
    //   let m = function(e){e.preventDefault();};
    //   document.body.style.overflow='hidden';
    //   document.addEventListener("touchmove",m,{ passive:false });//禁止页面滑动
    // },
    // //开启页面滚动
    // Move(){
    //   let m =function(e){e.preventDefault();};
    //   document.body.style.overflow='';//出现滚动条
    //   document.removeEventListener("touchmove",m,{ passive:true });
    // }

  },
}
</script>

<style scoped>
*{
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
nav{
  background: rgba(0,0,0,0.1);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 90px;
  width: 100%;
  padding: 0 100px;
  position: fixed;
  top: 0%;
  z-index: 999;
  transition: all 0.3s ease;
  border-bottom: #afafaf 1px solid;
}
.navActive{
  background-image: linear-gradient(45deg, #654c35, #9c8161);
  /* background-image: linear-gradient(45deg, #202835, #283a54)!important; */
  height: 66px;
  position: fixed;
  border-bottom: none;
}
.navLogoActive{
  font-size: 27px!important;
}
nav .logo{
  background-image: linear-gradient(to right, rgb(89, 89, 89),rgb(255, 255, 255));
  -webkit-background-clip: text;
  color: transparent;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: -1px;
  transition: all 0.3s linear;
}
nav .logo span{
  background-image: linear-gradient(to right, orange, purple);
  -webkit-background-clip: text;
  color: transparent;
  font-size: 30px;
  transition: all 0.1s linear;
}
nav .logoPic{
  color: #fff;
  width: 13%;
  min-width: 167px;
  height: 100%;
  font-weight: 200;
  position: relative;
  padding: 10px;
  box-sizing: border-box;
  transition: all 0.3s ease;
}
nav .logoPic img{
  height: 80%;
  position: absolute;
}
.logoActive{
  height: 70px !important;
}
nav .logoText h1{
  width: 100px;
  font-size: 20px;
  position: absolute;
  /* display: inline-block; */
  left: 70px;
  top: 10px;
}
nav .logoText h2{
  width: 100px;
  font-size: 10px;
  position: absolute;
  /* display: inline-block; */
  left: 70px;
  top: 36px;
}
nav .nav-items{
  display: flex;
  flex: 1;
  list-style: none;
  padding: 0 0 0 40px;
}
nav .nav-items li{
  padding: 0 15px;
}
nav .nav-items li a{
  display: block;
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  text-decoration: none;
  transition: all 0.3s ease;
  font-family: "XingKai";

}
nav .nav-items li a:hover{
  color: #bba28c;
}
nav form{
  display: flex;
  height: 40px;
  border-radius: 10px;
  padding: 2px;
  /* background: #313635; */
  background: #f1ece5;
  min-width: 18% !important;
  border: 1px solid #f1ece5;

}
nav form .search-data{
  height: 100%;
  width: 100%;
  padding: 0 10px;
  color: rgb(52, 52, 52);
  font-weight: 500;
  background: none;
  border: none;
  font-size: 17px;
  font-family: "XingKai";

}
nav form button{
  padding: 0 15px;
  background: #83572e;
  border: none;
  cursor: pointer;
  font-size: 17px;
  color: #fff;
  border-radius: 7px;
  transition: all 0.3s ease;
}
nav form button:hover{
  background: #6a4315;
}
nav .nav-login{
  list-style: none;
  padding: 0 0 0 20px;
}
.nav-login .el-button i:hover{
  transition: all 0.2s ease;
  color: #57350e;
}
nav .nav-login li{
  padding: 0 15px;
}
nav .nav-login li a{
  color: #fff;
  font-family: "XingKai";
  font-size: 30px;
  font-weight: 500;
  text-decoration: none;
}
nav .menu-icon,
nav .search-icon,
nav .cha-icon{
  width: 40px;
  color: #fff;
  text-align: center;
  margin: 0 50px;
  font-size: 20px!important;
  font-weight: 700!important;
  cursor: pointer;
  display: none;
}
nav .menu-icon span,
nav .search-icon,
nav .cha-icon{
  display: none;
}
@media (max-width: 1245px){
  nav{
    padding: 0 50px;
  }
}
@media (max-width: 1140px){
  nav{
    padding: 0px;
  }
  nav .logo{
    flex: 2;
    text-align: center;
  }
  nav .nav-items{
    position: fixed;
    z-index: 999;
    top: 66px;
    width: 100%;
    left: -100%;
    height: 100%;
    background-image: linear-gradient(45deg, #644420, #654c35);
    display: inline-block;
    padding: 10px 50px 0 50px;
    text-align: center;
    transition: left 0.3s ease;
  }
  nav .nav-items.active{
    left: 0%;
  }
  nav .nav-items li{
    line-height: 40px;
    margin: 30px 0;
  }
  nav .nav-items li a{
    font-size: 23px;
  }
  nav form{
    position: absolute;
    top: 80px;
    right: 50px;
    z-index: 999;
    opacity: 0;
    pointer-events: none;
    transition: top 0.5s ease, opacity 0.5s ease;
  }
  nav form.active{
    opacity: 1;
    top: 95px;
    z-index: 999;
    pointer-events: auto;
  }
  nav form:before{
    position: absolute;
    content: "";
    top: -13px;
    right: 0;
    width: 0;
    height: 0;
    z-index: -1;
    margin: -20px 0 0;
    /* border: 10px solid transparent; */
    border-bottom-color: #f1ebe5;
  }
  nav form:after{
    position: absolute;
    content: "";
    height: 60px;
    width: calc(100% + 20px);
    padding: 2px;
    background: #f1ebe5;
    border-radius: 10px;
    z-index: -2;
    left: 50%;
    top: 20px;
    transform: translate(-50%, -50%);
  }
  nav .menu-icon{
    display: block;
  }
  nav .search-icon,
  nav .menu-icon span{
    display: block;
  }
  nav .search-icon.hide,
  nav .nav-login,
  nav .menu-icon span.hide{
    display: none;
  }
  nav .cha-icon.show{
    display: block;
  }
}
@media (max-width: 980px) {
  nav .search-icon,
  nav .menu-icon,
  nav .cha-icon{
    margin: 0 10px;
    font-size: 16px;
  }
  nav form{
    right: 30px;
  }
}
@media (max-width: 350px) {
  nav .search-icon,
  nav .menu-icon,
  nav .cha-icon{
    margin: 0 10px;
    font-size: 16px;
  }
}


.el-form-item {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
  margin: 26px 0;
}
.el-container{
    display: flex;
    border-radius: 5px;
}
.icon-container{
    padding: 0 10px;
    color: #fff;
    font-size: 20px;
}
.el-input ::v-deep .el-input__inner{
    border: none;
    background: #0000;
    color: #fff;
}
::v-deep .el-dialog__body{
  min-height: 320px;
}
::v-deep .el-dialog{
  background: #baa38e;
  border-radius: 15px;
}
.el-button{

}

@media (min-width: 1699px) {
  ::v-deep .el-dialog__body{
    min-height: 450px;
  }
  .root{
    min-height: 400px;
  }
}

.avatar_pic{
  /* display: flex; */
  /* background-size: 100% 100%; */
  width: 40px;
  height: 40px;
  margin-top: 15px;
  margin-right: 7px;
  border: #363431 1px solid;
  border-radius: 35px;
}
.nav-name{
  margin-left: 20px;
  display: flex;
  height: 70px;
  color: #fff;
  line-height: 70px;
  text-overflow: ellipsis;  /* ellipsis:显示省略符号来代表被修剪的文本  string:使用给定的字符串来代表被修剪的文本*/ 
  white-space: nowrap;   /* nowrap:规定段落中的文本不进行换行   */ 
  overflow: hidden; /*超出部分隐藏*/ 
}
.el-dropdown-link {
  font-size: 20px;
  cursor: pointer;
  color: #ffffff;
  font-family: "XingKai";
  transition: all 0.2s ease;
}
.el-icon-arrow-down {
  font-size: 18px;
  font-family: "XingKai";
}
.el-dropdown-link i:hover{
  color: #57350e;
}
.el-dropdown-menu{
  background: #ebe3db;
}
.el-dropdown-menu__item{
  color: #654c35;
  padding: 0 20px;
}
.el-dropdown-menu__item:hover{
  background: #d4c9bf !important;
  color: #654c35 !important;
}
[class*=" el-icon-"], [class^=el-icon-]{
  font-weight: 700!important;
}
</style>