import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import { Message } from 'element-ui'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path:'/home',
    component: () => import('@/views/Home')
  },
  {
    path:'/register',
    // webpack提供import函数来路由懒加载导入组件
    //路由懒加载：页面路由路径切换到/register，采取加载对应组件代码
    component: () => import('@/views/Register')
  },
  {
    path:'/login',
    component: () => import('@/views/Login')
  },
  {
    path:'/waterfall',
    component: () => import('@/views/WaterFall')
  },
  {
    path:'/personal',
    component: () => import('@/views/Personal')
  },
  {
    path:'/creator',
    component: () => import('@/views/Creator')
  },
  {
    path:'/maininfo',
    component: () => import('@/views/MainInfo')
  },
  {
    path:'/aboutus',
    component: () => import('@/views/AboutUs')
  },
  {
    path:'/addorgan',
    component: () => import('@/views/AddOrgan')
  },
  {
    path:'/normalherb',
    component: () => import('@/views/NormalHerb')
  },
  {
    path:'/test',
    component: () => import('@/views/Test')
  },
  {
    path:'/chinamap',
    component: () => import('@/views/ChinaMap')
  },
  {
    path:'/comments',
    component: () => import('@/views/comments')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 全局前置路由守卫
router.beforeEach((to,from,next)=>{
  const token = store.state.token
  if(to.path === '/aboutus' || to.path === '/creator' || to.path === '/personal'){
    if(!token){
      router.push('/home')
      Message({
        type: 'error',
        message: '请先登录！'
      })
    }else{
      next()
    }
  }else{
    next()
  }
})

export default router
