// //封装的是具体的接口请求方法
// //每个方法只负责请求一个 url 地址
import request from '@/axios'
import store from '@/store'

//注册接口
export const registerAPI = params =>{
    return request({
        url: '/api/reguser',
        method: 'POST',
        // params的对象参数名和值，axios源码会把参数和值，拼接在url?后面拼接给后台（query查询字符串）
        // data的对象参数名和值，axios源码会把参数和值，拼接在请求体里（body）参数
        data:params
    })
}

//登录接口
export const loginAPI = params =>{
    return request({
        url: '/api/login',
        method: 'POST',
        data: params
    })
}

//获取用户信息接口
export const getUserInfoAPI = () => {
    const token = localStorage.getItem('token')
    return request({
        url: '/admin/getUserInfo',
        method: 'GET',
    })
}

//更新用户数据
export const updateUserInfoAPI = (params) => {
    return request({
        url:'/admin/updateUserInfo',
        method:'post',
        data: params
    })
}

//更新用户头像
export const updateUserAvatarAPI = (avatar) => {
    // console.log(avatar);
    return request({
        url:'/admin/updateAvatar',
        method: 'POST',
        data: {
            avatar
        }
    })
}

